:root {
	--baseline: 8;
	--heading-font: 'Montserrat', sans-serif;
	--copy-font: 'Heebo', sans-serif;
	--background-body: #e8ecf1;
	--color-body: #333;
	--color-accent: #ffcb05;
	--color-alabaster: #fbfbfb;
	--color-sand: #f5f5f5;
	--color-concrete: #f3f3f3;
	--color-mercury: #e6e5e5;
	--color-alto: #d0d0d0;
	--color-bombay: #b1b1b3;
	--color-boulder: #767676;
	--color-tundora: #3a3b3a;
	--grid-max-width: 960px;
	--grid-outer-padding: 1rem;
}
html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
